/* Global CSS */
* {
  box-sizing: border-box;
  font-family: "Lobster", "Merienda", cursive, sans-serif;
  line-height: 150%;
}

html,
body,
#root {
  padding: 0;
  margin: 0;
  height: 100%;
}

.game-row {
  margin-bottom: 24px;
}

.nav-text {
  padding: 20px;
  font-size: 150%;
  background-color: #2d3436;
  border: 1px double white;
}

.navbar {
  background-color: #d63031;
}

header {
  margin-top: 92px;
}

#jumboContainer {
  padding-top: 20px;
}

.hollywoo-title {
  font-family: "Bowlby One SC";
  color: #fdcb6e;
  text-shadow: -1px 0 #636e72, 0 1px #636e72, 1px 0 #636e72, 0 -1px #636e72;
}

.jumbotron {
  background-color: #2d3436;
  color: white;
}

.clicks-title {
  color: #d63031;
  font-size: 95%;
  text-shadow: 1.5px 1.5px 3.5px #dfe6e9;
}

.lead {
  letter-spacing: 1.5px;
}

main {
  background-color: white;
}

.click-item {
  width: 200px;
  height: auto;
  display: block;
}

img {
  border-color: #2d3436 !important;
}

img:hover {
  box-shadow: 5px 10px 18px #2d3436;
}

.current-score,
.top-score {
  padding: 24px;
}

.lastDiv {
  padding: 24px;
  margin: 0 auto;
  text-align: center;
}

#quote {
  font-size: 24px;
  font-family: "Merienda", cursive;
  color: #d63031;
}

